import React, { Component } from "react";
import {Helmet} from 'react-helmet'

class PageHelmet extends Component{
    render(){
        return(
            <React.Fragment>
                <Helmet>
                    <title>{this.props.pageTitle} Bauer Sites</title>
                    <meta name="description" content="Bauer comunicao agora  Bauer Sites. Criando sites e solues para pequenas e mdias empresas, alm de profissionais liberais e autnomos em busca de um espao na internet ." />
                </Helmet>
            </React.Fragment>
        )
    }
}


export default PageHelmet;
