import React, { Component , Fragment } from "react";
import FooterTwo from "../component/footer/FooterTwo";
import Helmet from "../component/common/Helmet";

const SlideList = [
    {
        textPosition: 'text-center',
        category: '',
        title: 'Bauer Sites ',
        description: 'Bauer Comunicao agora se chama Bauer Sites! Estamos prontos para ajudar voc e seu negcio a ter um site na internet.',
        buttonText: 'Venha visitar',
        buttonLink: 'https://www.bauersites.com.br'
    }
]

class BauerSites extends Component{
    constructor(props) {
        super(props);

        window.addEventListener('load', function() {
            console.log('All assets are loaded');
        })
    }
    render(){        
        return(
            <Fragment>
                <Helmet pageTitle="Bauer Comunicao, agora somos" />
                
                {/* Start Header Area  */}
                {/* End Header Area  */}

                {/* Start Slider Area   */}
                <div className="slider-activation slider-creative-agency" id="home">
                    <div className="bg_image bg_image_full bg_image--41" data-black-overlay="6">
                        {SlideList.map((value , index) => (
                            <div className="slide slide-style-2 slider-paralax d-flex align-items-center justify-content-center" key={index}>
                                <div className="container">
                                    <div className="row">
                                        <div className="col-lg-12">
                                            <div className={`inner ${value.textPosition}`}>
                                                {value.category ? <span>{value.category}</span> : ''}
                                                {value.title ? <h1 className="title theme-gradient">{value.title}</h1> : ''}
                                                {value.description ? <p className="description">{value.description}</p> : ''}
                                                {value.buttonText ? <div className="slide-btn"><a className="rn-button-style--2 btn-primary-color" href={`${value.buttonLink}`}>{value.buttonText}</a></div> : ''}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
                {/* End Slider Area   */}

                {/* Start Service Area  */}
                {/* End Service Area  */} 

                {/* Start About Area */}
                {/* End About Area */}

                {/* Start Portfolio Area */}
                {/* End Portfolio Area */}

                {/* Start CounterUp Area */}
                {/* End CounterUp Area */}

                {/* Start Team Area  */}
                {/* End Team Area  */}

                {/* Start Testimonial Area */}
                {/* End Testimonial Area */}

                {/* Start Blog Area */}
                {/* End Blog Area */}

                {/* Start Contact Us */}
                {/* End Contact Us */}

                {/* Start Brand Area */}
                {/* End Brand Area */}

                {/* Start call To Action  */}
                {/* End call To Action  */}

                {/* Start Footer Style  */}
                {/* End Footer Style  */}

                {/* Start Back To Top */}
                {/* End Back To Top */}
            </Fragment>
        )
    }
}

export default BauerSites;